import React, { Component } from 'react'
import { Carousel, Autocomplete, Datepicker, Dropdown, Modal, FormSelect } from 'materialize-css/dist/js/materialize.min.js'
import BusPassInfo from './BusPassInfo'
import VisitorModel from './VisitorModel'

let autocompleteData = {
    "Goa": null,
    "Varanasi": null,
    "Mumbai ( Shivaji Maharaj Airport )": null,
    "Delhi ( Delhi International Airport )": null
}

class BusCaro extends Component {

    state = {
        fromCity: '',
        toCity: '',
        date: null,
        busClass: '',
        busAndPassInfo: {
            adults: 1,
            childs: 0,
            infants: 0,
            travelType: "Seater"
        }
    }

    componentDidMount(){
        if(window.screen.availWidth > 750){
            const flightCaro = document.querySelector('.bus-caro');
            let instance = Carousel.init(flightCaro,{
                fullWidth: true,
                duration: 400
            })

            setInterval(()=>{
                instance.next()
            },6000)
        }

        var fromCity = document.querySelector('.fromCity');
        Autocomplete.init(fromCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    fromCity: val
                })
            }
        })

        var toCity = document.querySelector('.toCity');
        Autocomplete.init(toCity,{
            data: autocompleteData,
            limit: 4,
            onAutocomplete: (val)=>{
                this.setState({
                    toCity: val
                })
            }
        })

        var checkInDate = document.querySelector('.checkInDate');
        Datepicker.init(checkInDate,{
            defaultDate: new Date(),
            minDate: new Date(),
            onSelect: (selectedDate)=>{
                this.setState({
                    date: selectedDate
                });
            }
        })

        var hotelType = document.querySelector('.hotel-type');
        FormSelect.init(hotelType,{

        })

        var flightInfo = document.getElementById('flight-info');
        this.flightDropdown =  Dropdown.init(flightInfo,{
            coverTrigger: false,
            constrainWidth: false,
            closeOnClick: false
        })

        var userInfo = document.getElementById('user-info');
        this.userDetailsModel =  Modal.init(userInfo,{
            
        })
    }

    handleClassChange = (e)=>{
        this.setState({
            busClass: e.target.options[e.target.selectedIndex].value
        })
    }

    getBusAndPassInfo = (info)=>{
        this.setState({
            busAndPassInfo: info
        })
        this.flightDropdown.close();
    }

    submitHotelData = ()=>{
        this.userDetailsModel.open();
    }

    sendAllDataToServer = (userData)=>{
        console.log(userData);
        console.log(this.state);
        this.userDetailsModel.close();
    }

    render(){
        let travellersInfo = this.state.busAndPassInfo
        let notravellers = travellersInfo.adults + travellersInfo.childs + travellersInfo.infants; 
        return (
            <div id="booking-activity">
                <div className="carousel carousel-silder default-caro booking-caro bus-caro">
                    <a className="carousel-item" href="#one!">
                        <div className="caro-item" id="caro-1"></div>
                    </a>
                    <a className="carousel-item" href="#two!">
                        <div className="caro-item" id="caro-2"></div>
                    </a>
                    <a className="carousel-item" href="#three!">
                        <div className="caro-item" id="caro-3"></div>
                    </a>  
                </div>

                <div id="flight-model" className="container">
                    <div id="flight-model-content">
                        <div id="flight-iconBox">
                            <i className="material-icons">departure_board</i>
                            <span>BUS</span>
                        </div>

                        <div className="row">
                            <div className="col s12 m9">
                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">location_on</i>
                                        <input type="text" id="form-city-input" className="autocomplete fromCity" placeholder="From"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">location_on</i>
                                        <input type="text" id="form-city-input" className="autocomplete toCity" placeholder="To"></input>
                                    </div>

                                    <div className="input-field col s12 m4">
                                        <i className="material-icons prefix">date_range</i>
                                        <input type="text" className="datepicker checkInDate" placeholder="Date"></input>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="input-field col s12 m4">
                                        <select className="hotel-type" onChange={this.handleClassChange}>
                                            <option value="" disabled selected>Choose Class</option>
                                            <option value="AC">AC</option>
                                            <option value="Non AC">Non AC</option>
                                            <option value="All">All</option>
                                        </select>                                
                                    </div>

                                    <div id="flight-info" className="col s12 m4 dropdown-trigger" data-target='pass-info'>
                                        <span>{ notravellers } Travellers, { travellersInfo.travelType }</span>
                                        <i className="material-icons">person</i>
                                    </div>

                                    <BusPassInfo getInfo={this.getBusAndPassInfo} />
                                </div>
                            </div>
                            <div className="col s12 m2 offset-m1">
                                <button onClick={this.submitHotelData} className="waves-effect waves-light btn blue" id="search">Search</button>
                            </div>
                        </div>
                    </div>
                </div>
                <VisitorModel sendData={this.sendAllDataToServer} />
            </div>
        )
    }
}

export default BusCaro;