import React, { Component } from 'react';
import BusCaro from './BusCaro';


class BusPassInfo extends Component {

    state = {
        adults: 1,
        childs: 0,
        infants: 0,
        travelType: "Seater"
    }

    handleFlightClassClick = (e)=>{
        this.setState({
            travelType: e.target.id
        })
    }

    getCheckedVal = (id)=>{
        if(this.state.travelType === id) return true;
        return false;
    }

    handleFlightClassChange = (e)=>{
        
    }

    handlePassengersAddition = (e)=>{
        let id = e.target.id;
        let maxVal = 50;
        if(id === "infants") maxVal = 30
        if(id === "childs") maxVal = 20
        this.setState({
            [id]: this.state[id] + 1 < maxVal ? this.state[id] + 1 : this.state[id]
        })
    }

    handlePassengersSubtraction = (e)=>{
        let id = e.target.id;
        this.setState({
            [id]: this.state[id] - 1 < 1 ? this.state[id] : this.state[id] - 1
        })
    }

    submitTheFlightInfo = ()=>{
        this.props.getInfo(this.state);
        // console.log(this.state);
    }

    render(){
        return (
            <div id="pass-info" className="dropdown-content z-depth-2">
                <div id="pass-wrapper">
                    <div id="passengers">
                        <h6>Passengers</h6>
                        <div id="passengers-list">
                            <h6>Adult (+12 yrs)</h6>
                            <div className="val">
                                <button className="btn blue" id="adults" onClick={this.handlePassengersSubtraction}>-</button>
                                <span>{ this.state.adults }</span>
                                <button className="btn blue" id="adults" onClick={this.handlePassengersAddition}>+</button>
                            </div>
                        </div>

                        <div id="passengers-list">
                            <h6>Child (2-12 yrs)</h6>
                            <div className="val">
                                <button className="btn blue" id="childs" onClick={this.handlePassengersSubtraction}>-</button>
                                <span>{ this.state.childs }</span>
                                <button className="btn blue" id="childs" onClick={this.handlePassengersAddition}>+</button>
                            </div>
                        </div>

                        <div id="passengers-list">
                            <h6>Infant (0-2 yrs)</h6>
                            <div className="val">
                                <button className="btn blue" id="infants" onClick={this.handlePassengersSubtraction}>-</button>
                                <span>{ this.state.infants }</span>
                                <button className="btn blue" id="infants" onClick={this.handlePassengersAddition}>+</button>
                            </div>
                        </div>
                    </div>

                    <div id="flight-class-info">
                        <h6>Bus class</h6>
                        <div>
                            <label>
                                <input id="Seater" className="with-gap valign-center" checked={this.getCheckedVal("Seater")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio" />
                                <span>Seater</span>
                            </label>
                            <label>
                                <input id="Semi Sleeper" className="with-gap valign-center" checked={this.getCheckedVal("Semi Sleeper")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio"  />
                                <span>Semi Sleeper</span>
                            </label>

                            <label>
                                <input id="Sleeper" className="with-gap valign-center" checked={this.getCheckedVal("Sleeper")} onClick={this.handleFlightClassClick} onChange={this.handleFlightClassChange} type="radio" />
                                <span>Sleeper</span>
                            </label>
                        </div>
                        <button className="btn blue waves-effect" id="apply" onClick={this.submitTheFlightInfo} >Apply</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default BusPassInfo;